.container{
    display: flex;
    width: 100%;
    justify-content:center;
    z-index: -1;
    margin-bottom: 1rem;
    .left-side{
     display: flex;
     flex-direction: column;
     gap: 3rem;
     padding: 0 30px;
     margin-top: 10rem;
    .title{
        cursor: pointer;
        color: white;
        font-size: 25px;
        span{
            color: greenyellow;
        }
    }
     .desc{
        p{
            line-height: 1.8rem;
            font-size: large;
            font-weight: 600;
        }
     }
     .cv{
        background-color: blue;
        color: white;
        padding: 10px 20px;
        border-radius: 8px;
        cursor: pointer;
        border: none;
        width: 8rem;
     }
    }
    .right-side{
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: -1;
        div{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 13rem;
            height: 15rem;
            margin-top: 6rem;
            border-radius: 50%;
            padding: 2rem;
        }
        img{
            width: 30rem;
            height: 30rem;    
        }
    }
    }
    
    @media screen and (max-width: 900px) {
       .container{
        .right-side{
            display: none;   
        }
        .left-side{
            .title{
                font-size: 20px;
            }
            .desc{
                p{
                    font-size: 13px;
                    line-height:1.2rem;
                }
            }
            .cv{
                background-color: blue;
                color: white;
                padding: 8px 15px;
                border-radius: 10px;
                width: 8rem;
             }
        }
       } 
    
       
    }