.Skills-container{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    gap: 2rem;
    .top{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 1rem;
        h2{
            text-align: center;
            color: lawngreen;
            &:hover{
                color: burlywood;
            }
        }
    }
    .bottom{
        display: flex;
        align-items: center;
        justify-content:space-between;
        width: 100%;
        gap: 2rem;
        .front-ent{
          width: 35%;
          margin-left: 10rem;
          h3{
            text-align: center;
            margin-bottom: 15px;
        }
          .persantage{ 
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 5px;
          }
            .skill-container{
                width: 100%;
                background-color:aliceblue;
                border-radius: 10px;
            }
            .skills{
             display: flex;
             width: 100%;
             height: 1px;
             padding: 5px 5px;
             margin-bottom: 20px;
             border-radius: 10px;
            }
            .html{ width: 95%; background-color: greenyellow; color: green;}
            .css{width: 90%; background-color: greenyellow; color: green;}
            .reactjs{ width: 60%; background-color: greenyellow; color: green;}
            .javascript{ width: 70%; background-color:greenyellow; color: green;}
            .wordpress{ width: 70%; background-color:greenyellow; color: green;}
        }
        .back-ent{
            width: 35%;
            margin-right: 10rem;
            h3{
                text-align: center;
                margin-bottom: 15px;
            }
            .persantage{ 
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 5px;}
            .skill-container{
                width: 100%;
                background-color:aliceblue;
                border-radius: 10px;
            }
            .skills{
                display: flex;
                width: 100%;
                height: 1px;
                padding: 5px 5px;
                margin-bottom: 20px;
                border-radius: 10px;
            }
            .nodejs{ width: 50%;  background-color:greenyellow; color: green;}
            .mongoDb{width: 60%; background-color:greenyellow; color:green;}
            .reactjs{ width: 50%; background-color:greenyellow; color: green;}
            .Mysql{ width: 50%; background-color:greenyellow; color: green;}
            .javascript{ width: 50%; background-color:greenyellow; color: green;}
            .wordpress{ width: 60%; background-color: greenyellow; color: green;} 
        }
    }
}
@media screen and (max-width:900px) {
    .Skills-container{
        .top{
            h2{
                margin: 0;
                text-align: center;
                color: lawngreen;
                &:hover{
                    color: burlywood;
                }
            }
            p{
                display: none;
            }
        }
        .bottom{
        display: flex;
        width: 100%;
        flex-direction: column;
        .front-ent{
            margin-left: 0;
            width: 50%;
        }
        .back-ent{
            margin-right: 0;
            width: 50%;
        }
    }
    } 
}