.footer-container{
    display: flex;
    flex-direction: column;
    background-color:aliceblue;
    width: 100%;
    padding: 10px;
    color:black;
    .top{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        margin-top: 10px;
        div{
            a{
                text-decoration: none;
            }
        h2{
            cursor: pointer;
        }
         }
           .about{
            display: flex;
            justify-content: space-between;
            gap: 20px;
            align-items: center;
            margin-top: 20px;
            cursor: pointer;
            a{
                text-decoration: none;
            }
           }
           .icons{
            display: flex;
            gap: 30px;
            align-items: center;
            justify-content: space-between;
            margin-top: 20px;
           }
    }
}
@media screen and (max-width:900px) {
    .footer-container{
      .top{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .about{
            display: flex;
        }
      }  
    }
}