.service-container{
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content:center;
    align-items: center;
    .top{
        h2{
            text-align: center;
            margin-bottom: 10px;
            color:greenyellow;
            &:hover{
                color: burlywood;
            }
        }
    }
    .bottom{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    
    .box{
        background-color:aliceblue;
        height: 12rem;
        margin: 10px;
        width: 16rem;
        padding: 2rem;
        color: black;
        border-radius: 1rem;
    }}
}
@media screen and (max-width:900px) {
    .service-container{
        .top{
            h2{
                text-align: center;
                color:greenyellow;
                &:hover{
                    color: burlywood;
                }
            }
            p{
                display: block;
            }
        }
        .bottom{
            display: flex;
            flex-direction: column;
            .box{
                height: 5rem; 
                margin: 0; 
            }
        }
    }
}