.resum-container{
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content:center;
    align-items: center;
    flex-direction: column;
    .top{
        h2{
            text-align: center;
            margin-bottom: 10px;
            color: greenyellow;
            &:hover{
                color:burlywood;
            }
        }
        margin-bottom: 2rem;
    }
    .bottom{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 3rem;
        .blog1,.blog2{
            p{
                line-height: 1.5;
            }
            background-color:aliceblue;
            margin: 0.5rem;
            padding: 1rem;
            border-radius: 0.5rem;
            color: black;
            margin-bottom: 1rem;
            height: 10rem;
        }
    }
}
@media screen and (max-width:900px) {
    .resum-container{
        .top{
            h2{
                text-align: center;
                color: greenyellow;
                margin: 0;
                &:hover{
                    color:burlywood;
                }
            }
            p{
                display: none;
            }
        }
        .bottom{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 0.5rem;
            .blog1,.blog2{
                background-color:aliceblue;
                padding: 1rem;
                border-radius: 0.5rem;
                color: black;
            }
        }
    }
    
}