.about__container{
    display: flex;
    height: 100vh;
    justify-content:center;
    align-items: center;
    margin: 0 auto;
    width: 70%;
    .left-side{
        flex: 1;
        .icons{
            background-color:aliceblue;
            width: 20%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            padding: 10px;
            height: 100%;
            color: black;
            flex-direction: column;
            gap: 20px;
            padding: 10px;
            border-radius: 8px;
        }
    }
    .right-side{
        flex: 3;
        h2{
            margin-bottom: 1rem;
            color:lawngreen;
            box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
            &:hover{
                color: burlywood;
            }
        }
        p{
            margin-bottom: 2rem;
        }
        .personel-info{
            div{
            display: flex;
            gap: 1.5rem;
            justify-content: flex-start;
            margin-bottom: 0.5rem;}
        }
        .project{
            color: lawngreen;
        }
        .cv{
            margin-top: 20px;
            button{
                background-color:blue;
                color: white;
                padding: 10px 20px;
                border-radius: 8px;
                border: none;
                width: 8rem;
                cursor: pointer;
            }
        }
    }
}
@media screen and (max-width:900px) {
    .about__container{
        .left-side{
         display: none;
        }
        .right-side{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
}
